import React,{Component} from 'react'
import ReactDOM from 'react-dom'
import {Button,Toast,Confirm,Action,Drawer} from '../../src/index'

ReactDOM.render(<Button onClick={function(){console.log('click!');}}></Button>,document.querySelector('.s-button-normal'));
ReactDOM.render(<Button onClick={function(){console.log('click!');}} disabled></Button>,document.querySelector('.s-button-normal-disabled'));

ReactDOM.render(<Button type="primary"></Button>,document.querySelector('.s-button-primary'));
ReactDOM.render(<Button type="primary" disabled></Button>,document.querySelector('.s-button-primary-disabled'));

ReactDOM.render(<Button type="ghost"></Button>,document.querySelector('.s-button-ghost'));
ReactDOM.render(<Button type="ghost" disabled></Button>,document.querySelector('.s-button-ghost-disabled'));

ReactDOM.render(<Button type="link"></Button>,document.querySelector('.s-button-link'));
ReactDOM.render(<Button type="link" disabled></Button>,document.querySelector('.s-button-link-disabled'));

ReactDOM.render(<Button size="small"></Button>,document.querySelector('.s-button-small'));
ReactDOM.render(<Button size="small" type="primary"></Button>,document.querySelector('.s-button-small-primary'));

ReactDOM.render(<Button size="mini"></Button>,document.querySelector('.s-button-mini'));
ReactDOM.render(<Button size="mini" type="primary"></Button>,document.querySelector('.s-button-mini-primary'));


ReactDOM.render(<div>
    <Button type="primary" size="small" onClick={function(){new Toast('');}}></Button>
    <Button type="primary" size="small" onClick={function(){new Toast('',{type:'success'});}}></Button>
    <Button type="primary" size="small" onClick={function(){new Toast('...',{type:'loading'});}}></Button>
</div>,document.querySelector('.s-dialog-toast'));
ReactDOM.render(<div>
    <Button type="primary" size="small" onClick={function(){new Confirm('');}}></Button>
    <Button type="primary" size="small" onClick={function(){new Confirm('',{callbackCancel:function(){}});}}></Button>
    <Button type="primary" size="small" onClick={function(){new Confirm('',{title:'',callbackCancel:function(){}});}}></Button>
</div>,document.querySelector('.s-dialog-confirm'));

ReactDOM.render(<div>
    <Button type="primary" size="small" onClick={function(){new Action([[{
        text:'',
        label:true
    },{
        text:'',
        callback:function(){

        }
    }],[{
        text:'',
        color:'red'
    }]]);}}></Button>
</div>,document.querySelector('.s-dialog-action'));

class DrawerContent extends Component{
    render(){
        const {remove}=this.props;
        return (
            <div>
                <h1>Drawer</h1>
                <Button size="small" onClick={remove}>Drawer</Button>
            </div>
        );
    }
}

ReactDOM.render(<div>
    <Button type="primary" size="small" onClick={function(){new Drawer(<div><h1>Drawer</h1></div>);}}></Button>
    <Button type="primary" size="small" onClick={function(){new Drawer(<div><h1>Drawer</h1></div>,{right:true});}}></Button>
    <Button type="primary" size="small" onClick={function(){new Drawer(<DrawerContent/>);}}></Button>
</div>,document.querySelector('.s-dialog-drawer'));


